<template>
    <div class="ts__calc__wrapp">
        <div class="ts__calc__processbar mb-4">
            <div class="row mb-3">
                <div v-for="(item, index) in steps" class="col-lg-2 text-center ts__calc__step__progress"
                     :class="{'ts__calc__active__progress':isActiveStep(index)}">
                    {{item.name}}
                </div>
            </div>

            <div class="ts__calc__processbar__line">
                <div class="ts__calc__processbar__trake" :style="{width:procentBar+'%'}"></div>
            </div>
        </div>

        <transition
                name="custom-classes-transition"
                enter-active-class="animated bounceInLeft"
                leave-active-class="animated bounceOutRight"
                mode="out-in"
                appear>

            <div v-if="step=='type'" class="ts__calc__step" key="type">
                <div class="ts__calc__step__title">Выберите крепёж:</div>
                <div class="row">
                    <div class="col-lg-8 offset-lg-2">
                        <div class="row">
                            <div v-for="item in types" v-if="types.length" class="col-md-3">
                                <div @click="setType(item.value)" :class="{'ts__calc__active__type':type==item.value}"
                                     class="ts__calc__box d-flex justify-content-center align-items-center">
                                    <div class="ts__calc__box__inher">
                                        <div class="ts__calc__icon"><i :class="item.icon"></i></div>
                                        <div class="ts__calc__name">{{item.name}}</div>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>

            <div v-if="step=='standard'" class="ts__calc__step" key="standard">
                <div class="ts__calc__step__title">Выберите стандарт:</div>
                <div v-for="(item, index) in db">
                    <check-box :option="index" :label="item.name" :key="'check-box'+index"
                               v-model="standard"></check-box>
                </div>

            </div>

            <div v-if="step=='diameter'" class="ts__calc__step" key="diameter">
                <div class="ts__calc__step__title">Выберите диаметр:</div>
                <div v-for="(item, index) in db[standard].diameters">
                    <check-box :option="index" :label="item.name" :key="'check-box-diameters'+index"
                               v-model="diameter"></check-box>
                </div>
            </div>

            <div v-if="step=='long'" class="ts__calc__step" key="long">
                <div class="ts__calc__step__title">Выберите длину:</div>
                <div v-for="(item, index) in db[standard].diameters[diameter].longs">
                    <check-box :option="item.weight" :label="item.long" :key="'check-box-longs'+index"
                               v-model="weight"></check-box>
                </div>
            </div>

            <div v-if="step=='koef'" class="ts__calc__step" key="koef">
                <div class="ts__calc__step__title">Выберите материал:</div>

                <div v-for="(item, index) in koefs">
                    <check-box :option="item.value" :label="item.name" :key="'check-box'+index"
                               v-model="koef"></check-box>
                </div>
            </div>

            <div v-if="step=='weight'" class="ts__calc__step" key="weight">
                <div class="ts__calc__step__title">ВЕС 1 ШТ. {{oneWeight}} г.</div>

                <div class="row">
                    <div class="col-md-4">
                        <div class="form-group">
                            <label>Вес</label>
                            <div class="input-group input-group-lg">
                                <input class="form-control" @input="setQuantity" :value="weightsumm" type="text">
                                <div class="input-group-append">
                                    <span class="input-group-text">КГ</span>
                                </div>
                            </div>

                        </div>

                        <div class="form-group">
                            <label>Количесво</label>
                            <div class="input-group input-group-lg">
                                <input class="form-control" @input="setWeightsumm" :value="quantity" type="text">
                                <div class="input-group-append">
                                    <span class="input-group-text">ШТ</span>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>


            </div>
        </transition>

        <div v-if="step!='type'" class="row">
            <div class="col-md-6">
                <button @click="prevStep()" type="button" class="btn btn-green">Назад</button>

                <button @click="reset()" type="button" class="btn btn-green ml-3">Сбросить</button>
            </div>

        </div>


    </div>
</template>

<script>
    import CheckBox from './components/CheckBox.vue'
    import axios from 'axios'
    import 'animate.css';
    import 'lodash'

    export default {
        name: 'app',
        components: {
            CheckBox
        },
        data(){
            return {
                steps: [
                    {
                        name: 'Шаг 1',
                        value: 'type'
                    },
                    {
                        name: 'Шаг 2',
                        value: 'standard'
                    },
                    {
                        name: 'Шаг 3',
                        value: 'diameter'
                    },
                    {
                        name: 'Шаг 4',
                        value: 'long'
                    },
                    {
                        name: 'Шаг 5',
                        value: 'koef'
                    },
                    {
                        name: 'Шаг 6',
                        value: 'weight'
                    },
                ],
                step: 'type',
                db: [],
                types: [],
                koefs: [],
                type: '',
                standard: '',
                diameter: '',
                koef: '',
                weight: '',
                quantity: '',
                weightsumm: '',
                host:'https://raw.githubusercontent.com/MazeCms/calc/master'
            }
        },
        created(){

            this.getTypes()
            this.getKoefs()
        },
        watch: {
            type(val){
                if (val !== '') {
                    axios.get(this.host+'/db/' + val + '.json').then((response) => {
                        this.db = response.data
                    this.nextStep()
                })
                }
            },
            standard(val){
                if (val !== '') {
                    this.nextStep()
                }
            },
            diameter(val){
                if (val !== '') {
                    this.nextStep()
                    if (this.db[this.standard].diameters[this.diameter].longs.length > 1) {

                    } else {
                        this.weight = this.db[this.standard].diameters[this.diameter].longs[0].weight
                    }

                }
            },
            koef(val){
                if (val !== '') {
                    this.nextStep()
                    this.weightsumm = this.getWeightsumm(1)
                    this.quantity = 1
                }
            },
            weight(val){
                if (val !== '') {

                    this.nextStep()
                }
            },


        },
        computed: {
            procentBar(){
                let index = this.steps.findIndex(item => {
                            return item.value == this.step
                        }
            )
                index += 1


                return Math.round((index / this.steps.length) * 100)
            },
            oneWeight(){
                return Math.round(this.weight * this.koef * 100) / 100
            }


        },
        methods: {
            nextStep(){
                let index = this.steps.findIndex(item => {
                            return item.value == this.step
                        }
            )
                index += 1
                if (index <= this.steps.length) {
                    this.step = this.steps[index].value
                }
            },

            getQuantity(val){
                return Math.round(val * this.koef * 1000 / this.weight)
            },


            getWeightsumm(val){
                return Math.round(val * this.koef * this.weight * 100) / 100000
            },
            setQuantity(e){
                this.weightsumm = Number(e.target.value)
                this.quantity = this.getQuantity(this.weightsumm)
            },
            setWeightsumm(e){

                this.quantity = Number(e.target.value)
                this.weightsumm = this.getWeightsumm(this.quantity)

            },
            prevStep(){
                let index = this.steps.findIndex(item => {
                            return item.value == this.step
                        }
            )

                if (index != -1) {
                    let prevIndex = index - 1
                    if (prevIndex >= 0) {
                        this.step = this.steps[prevIndex].value
                    }

                    if (index == 1) {
                        this.type = ''
                    }
                    else if (index == 2) {
                        this.standard = ''
                    }
                    else if (index == 3) {
                        this.diameter = ''
                    }
                    else if (index == 4) {

                        this.weight = ''
                    }
                    else if (index == 5) {

                        this.koef = ''
                    }
                    else if (index == 6) {
                        this.quantity = ''
                        this.weightsumm = ''
                    }
                }


            },
            setType(type){
                if (this.type == type) {
                    this.type = ''
                } else {
                    this.type = type
                }
            },
            isActiveStep(curIndex){
                let index = this.steps.findIndex(item => {
                            return item.value == this.step
                        }
            )

                if (index !== -1) {
                    if (curIndex <= index) {
                        return true
                    }
                }
                return false
            },
            getTypes(){
                axios.get(this.host+'/db/type.json').then((response) => {

                    this.types = response.data
            })
            },
            getKoefs(){
                axios.get(this.host+'/db/koef.json').then((response) => {

                    this.koefs = response.data
            })
            },
            reset(){
                this.type = ''
                this.standard = ''
                this.diameter = ''
                this.weight = ''
                this.koef = ''
                this.step = 'type'
            }
        }
    }
</script>
