<template>
    <div class="ts__checkbox__wrrapp" @click="setValue()">
        <div :data-value="value" :data-option="option" class="ts__checkbox__cub" :class="{'ts__checkbox__cub__active':option===value}"></div>
        <div class="ts__checkbox__label">{{label}}</div>
    </div>

</template>

<script>
    export default {
        name: 'CheckBox',
        props: {
            label: String,
            value: [String, Number],
            option: [String, Number],
        },
        methods: {
            setValue(){

                if (this.option === this.value) {
                    this.$emit('input', '')
                } else {
                    this.$emit('input', this.option)
                }

            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
    .ts__checkbox__wrrapp {
        display: flex;
        align-items: center;
        cursor: pointer;
        margin-bottom: 10px;
    }

    .ts__checkbox__cub {
        width: 22px;
        height: 22px;
        border:1px solid #66b32c;
        -webkit-transition: all 0.5s ease-out;
        -moz-transition: all 0.5s ease-out;
        -o-transition: all 0.5s ease-out;
        transition: all 0.5s ease-out;
    }
    .ts__checkbox__cub__active{
        background-color: #66b32c;
    }
    .ts__checkbox__label{
        margin-left: 20px;
        font-size: 16px;
    }
</style>
